import React from "react";
import { useNavigate } from "react-router-dom";
import { Button, Result } from "antd";

function Empty() {
  const navigate = useNavigate();

  const back = () => {
    navigate("/", {
      replace: true,
    });
  };

  return (
    <Result
      status="404"
      title="404"
      subTitle="访问的页面不存在."
      extra={
        <Button type="primary" onClick={back}>
          返回首页
        </Button>
      }
    />
  );
}

export default Empty;
